.card {
    border: none;
}
* {
  text-decoration: none;
  list-style: none;
}

.App {
  height: 100%;
  padding: 5em 0;
  background-image: -webkit-linear-gradient(45deg, #1e1d2e, #394b6f);
  background-image: linear-gradient(45deg, #1e1d2e, #394b6f);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App .btn.bth-red {
  background-color: #bd081c;
}
.video-item {
  margin-bottom: 30px;
  cursor: pointer;
  background-color: #000;
  box-shadow: 3px 3px 10px #21203a;
}
.video-item img {
  width: 100%;
}
.video-item .video-body {
  padding: 1.5rem;
  border: 1px solid #666;
  background-color: #fff;
}
.video-item .video-title {
  font-size: 1.2em;
  font-weight: 600;
  line-height: 1.5em;
  color: #333;
}
.video-item .video-writter {
  font-size: 1em;
  color: #999;
}
.video-detail .video-body {
  padding: 2rem;
  color: #333;
  border: 1px solid #666;
  background-color: #fff;
}
.video-detail .video-body .video-title {
  font-size: 1.3em;
  font-weight: 600;
  line-height: 1.5em;
  color: #333;
}
.video-detail .video-body .video-writter {
  display: inline-block;
  padding-right: 20px;
  font-size: 1em;
  color: #666;
}
.video-detail .video-body .video-date {
  display: inline-block;
  font-size: 1em;
  color: #666;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



